@charset "utf-8";
@import "../../static/m/sass/px2rem";
.weui-navbar{
    background-color: #fff;
    &:after{}
}
.weui-navbar__item{
    font-size: px2rem(32);
    &:after{
        border: none;
    }
    &.weui-bar__item_on{
        background-color: #fff;
        color: #0099DD;
        border-bottom: 4px solid #0099DD;
    }
}
.react-weui-infiniteloader{
    padding-top: px2rem(70);
}
.weui-tab{
    // position: absolute;
    // top: px2rem(90);
    // bottom: 0;
    // height: auto;
    // width: 100%;
    .weui-panel{
        margin-top: 0;
    }
    .text-dark{
        font-size: px2rem(32);
        margin-right: px2rem(20);
    }
    .fa{
        font-size: px2rem(32);
        margin-right: px2rem(20);
    }
    .label{
        margin-right: px2rem(8);
        position: relative;
        top: px2rem(-5);
    }
    .weui-form-preview{
        &:before{
            left: px2rem(28);
            right: px2rem(28);
        }
    }
    .weui-form-preview__bd{
        position: relative;
        .label{
            text-align: center;
            min-width: 4em;
            margin: px2rem(10) px2rem(8) px2rem(10) 0;
            top: 0;
        }
        .tag-wrap{
            position: absolute;
            right: px2rem(30);
            top: px2rem(-60);
            width: px2rem(160);
            height:px2rem(160);
            img{
                width: 100%;
            }
        }
    }
    .weui-form-preview__label{
        font-size: px2rem(28);
        color: #a4a5a9;
        margin-right: px2rem(20);
    }
    .weui-form-preview__value{
        font-size: px2rem(28);
        color: #404348;
        font-style: normal;
        text-align: left;
    }
    .weui-form-preview__ft{
        &:after{
            left: px2rem(28);
            right: px2rem(28);
        }
    }
    .previewFootInfo{
        padding: 0 px2rem(30);
        color: #74767a;
        font-size: px2rem(28);
    }
    .weui-panel__hd{
        .previewFootInfo{
            padding: 0;
        }
    }
    .label-wrap{
        //padding-top: px2rem(8);
        text-align: left;
        .label{
            margin-top: 0;
        }
    }
    .weui-dialog{
        .weui-dialog__bd{
            text-align: left;
            padding-top: px2rem(0);
        }
        &.textAlignCenter{
            .weui-dialog__bd{
                text-align: center;
            }
        }
        &.textInset{
            .weui-dialog__bd{
                text-align: center;
                padding-top: px2rem(30);
            }
        }
    }
}
.popupTop{
    bottom: auto;
    top: px2rem(90);
    transform: translate(0, -250%);
    background-color: #fff;
    .filterBox{
        .weui-cells{
            margin-top: 0;
            background-color: #F7F8F9;
            .weui-cell__hd{
                &:after{
                    border: none;
                }
                .weui-select-val{
                    display: block;
                    text-align: center;
                    width: px2rem(120);
                    color: #009ADC;
                    padding: 0;
                    font-size: px2rem(28);
                }
            }
            .weui-cell__bd{
                padding-left: 0;
            }
        }
        .weui-btn-area{
            margin-top: px2rem(30);
            margin-bottom: px2rem(30);
        }
        .weui-cell_select{
            padding: px2rem(20) 0;
        }
        .weui-input{
            width: px2rem(560);
            border: 1px solid #E6E5EA;
            background-color: #fff;
            font-size: px2rem(28);
            color: #404348;
            height: px2rem(60);
            border-radius: px2rem(10);
            padding: 0 px2rem(15);
        }
    }

}
.weui-mask{
    top: px2rem(90);
}
.weui-popup_toggle{
    transform: translate(0, 0);
}


