.attachment-item,
.attachment-add{
    float: left;
    height: 50px !important;
    width: 155px !important;
    padding: 10px !important;
    margin: -1px 0 0 !important;

}
.ui-fileupload{
    //float: left;
    height: 100% !important;
}
.photo-item .upload-btn .u_btn{
    margin-top: 0 !important;
    padding-top: 39.5px;
}
.ui-fileupload{
    input[type="file"]{
        z-index: 5;
    }
}
.attachment-item{
    border: 1px solid transparent;
.close-btn{
    display: none;
    position: absolute;
    right: 0;
    top: 0;
    width: 0;
    height: 0;
    border-width: 14px;
    border-style: solid solid dashed dashed;
    border-color: #ff6655 #ff6655 transparent transparent;

.fa-times{
    position: absolute;
    top: -13px;
    right: -13px;
    color: #fff;
    font-size: 14px;
}
}


&:hover{
     background-color: #eaf9ff;
     border: 1px solid #d6f3ff;

.close-btn{
    display: block;
}
}
}


