@charset "UTF-8";

@import "px2rem";

html, body,.ui-box{
    width: 100%;
    height: 100%;
    font-size: px2rem(22);
    background-color: #EFEFF4;
}

.clearfix:before,
.clearfix:after{
    display: table;
    content: " ";
}
.clearfix:after{
    clear: both;
}
.disNone{
    display: none !important;
}

.hidden{
    display: none !important;
}
.disTableCell{
    display: table-cell;
    vertical-align: middle;
}

.btn-block{
    background-color: #fff;
    border: 0;
    color: #0099dd;
    border-radius: 0;
    font-size: px2rem(32);
    &:after{
        border-radius: 0;
    }
}

.partSpace{
    height: px2rem(30);
    background-color: #EBEDED;
}
.partSpace20 {
    height: px2rem(20);
}
input[placeholder], [placeholder], *[placeholder],select{
    color: #666;
    font-size: px2rem(32);
}


/*æ ç­¾*/
.label{
    display: inline-block;
    border-radius: px2rem(20);
    padding: px2rem(2) px2rem(25);
    font-size: px2rem(22);
    vertical-align: middle;
    color: #fff;

    &.label-info {
        background: #00CCEE;
    }
    &.label-success {
        background: #00BB88;
    }
    &.label-default {
        background-color: #74767A;
    }
    &.label-primary {
        background: #0099dd;
    }
    &.label-warning {
        background: #FFAA33;
    }
    &.label-danger {
        background: #FF6655;
    }
}

/*æå­*/
.text-dark{
