    font-size: px2rem(30);
    .weui-dialog__bd {
        padding-top: px2rem(40);
        line-height: px2rem(42);
        color: #74767a;
        [class^="weui-icon-"],
        .check-sucess {
            font-size: px2rem(75);
            color: #0b8;
            margin-bottom: px2rem(30);
        }
    }
}

