@charset "utf-8";
@import "../../static/m/sass/px2rem";
.header{
    $h : px2rem(90);
    background-color: #383B3F;
    height: $h;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 99;
    .btn-simple{
        position: absolute;
        top: 0;
        text-decoration: none;
        font-size: px2rem(32);
        color: #fff;
        height: $h;
        line-height: $h;
    }
    .header-left{
        left: px2rem(20);
    }
    .btn-back{
        padding-left: px2rem(30);
        &:after{
            font-family: 'FontAwesome';
            content: "\f104";
            position: absolute;
            font-size: px2rem(45);
            display: block;
            width: px2rem(25);
            top: px2rem(-2);
            left: 0;
            height: px2rem(45);
        }
    }
    .header-right{
        right: px2rem(20);
    }
    //å¤´é¨æ é¢
    .ui-title{
        text-align: center;
        margin: 0 px2rem(120);
        height: $h;
        line-height: $h;
        font-size: px2rem(36);
        color: #fff;
    }
}

.filterBox{
    li{
        height: px2rem(90);
        line-height: px2rem(90);
        background-color: #fff;
        color: #000;
        border-bottom: 1px solid #ddd;
        padding: 0 px2rem(30);
        font-size: px2rem(28);
    }
}


