.twoCode-dl {
  width: 500px;
  height: 300px;
  margin: 20px auto;
  padding-left: 30px;
}

.twoCode-dl dt {
  width: 300px;
  height: 300px;
  margin-right: 20px;
  float: left;
}

.twoCode-dl dd {
  float: left;
  width: 140px;
  margin-top: 15px;
}

.twoCode-dl dd b {
  font-size: 16px;
  margin: 10px 0 25px;
  display: block;
}

.twoCode-dl dd p {
  line-height: 26px;
  font-size: 14px;
}

.twoCode-dl dd button {
  margin-top: 55px;
}




